// ------- start header Section -------//
header {
  @extend %header;
}
/* -------- End header Section --------*/
/* ------- Start Landing Section ------*/
.landing {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  height: calc(100vh - 60px);
  background: radial-gradient(#fff, #ffd6d6);
  .container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    text-align: center;
    height: 100%;
    @include breakpoint(laptop) {
      flex-direction: row;
      column-gap: 50px;
    }
  }
  .intro-text {
    max-width: 555px;
    h1 {
      font-size: 35px;
      margin: 0 auto;
      text-transform: capitalize;
    }
    p {
      margin: 15px 0;
      color: $txt-clr;
    }
    a {
      @extend %btn;
      i {
      }
    }
  }
  .img-holder {
    flex-basis: 50%;
    max-width: 555px;
  }
}
/* -------- End Landing Section -------*/
/* ------- Start Features section -----*/

/* ------- Start Footer section -------*/
footer {
  @extend %footer;
}
/* -------- End Footer section --------*/
